import React from "react";

import chrome from "../../assets/images/companies/chrome.svg";
import expo from "../../assets/images/companies/expo.svg";
import meta from "../../assets/images/companies/meta.svg";
import stripe from "../../assets/images/companies/stripe.svg";
import uber from "../../assets/images/companies/uber.svg";

const logos = {
  chrome,
  expo,
  meta,
  stripe,
  uber,
} as const;

const renderLogo = (logoName: keyof typeof logos, className?: string) => (
  <img
    key={logoName}
    src={logos[logoName]}
    alt={logoName}
    className={`opacity-50 ${className}`}
  />
);

const CompanyLogo = ({
  name,
  className,
}: {
  name: keyof typeof logos | "all";
  className?: string;
}) => {
  if (name === "all") {
    return (
      <>
        {Object.keys(logos).map((logoName: keyof typeof logos) =>
          renderLogo(logoName, className)
        )}
      </>
    );
  }
  return renderLogo(name, className);
};

export default CompanyLogo;
