import React, { useEffect, useState } from "react";
// eslint-disable-next-line import/no-named-as-default
import Typist from "react-typist";

import heroIllustrationImg from "../../assets/images/hero-illustration.svg";
import CompanyLogo from "../CompanyLogo";
// import { ArrowRight } from "../Icons";

const BASE_URL = "https://withsutro.com";

const HERO_HEADING_TEXTS = [
  "class booking marketplace",
  "sneaker marketplace",
  "interest-based social app",
  "coworking space app",
  "rental booking app",
  "video-sharing app",
  "subscription content app",
];

// this is dual-written in the `/apps/studio/waitlist/helpers.ts`
const WAITLIST_CODES = [
  "twitter25",
  "reddit25",
  "facebook25",
  "discord25",
  "slack25",
  "linkedin25",
  "ben10",
  "hirad20",
  "haydenskironi20",
  "sutro25",
];

const TYPING_DELAY = 350;
const TYPING_BACKSPACE_DELAY = 1100;

const SHOW_WAITLIST = false;

const LoopedTypist = ({ children }: { children: any }) => {
  const [isTyping, setIsTyping] = useState(true);

  const handleTypingRestart = () => {
    setIsTyping(false);

    setTimeout(() => {
      setIsTyping(true);
    }, TYPING_DELAY * 2);
  };

  return (
    // Setting min-height to circumvent layout jumps as a result of new texts being replaced.
    <div className="min-h-[5.3rem]">
      {isTyping ? (
        <Typist
          cursor={{
            show: false,
          }}
          onTypingDone={handleTypingRestart}
        >
          {children}
        </Typist>
      ) : null}
    </div>
  );
};

const isValidEmail = (email: string) => {
  const filter =
    /^\s*[\w\-\+_]+(\.[\w\-\+_]+)*\@[\w\-\+_]+\.[\w\-\+_]+(\.[\w\-\+_]+)*\s*$/;
  return String(email).search(filter) != -1;
};

const HeroSection = () => {
  const [email, _setEmail] = useState<string>("");
  const [error, setError] = useState<string>("");
  const [code, setCode] = useState<string>("");
  const [isProdDown, setIsProdDown] = useState<boolean>(false);

  const setEmail = (text: string) => {
    // reset error state once they modify email
    if (error !== "") {
      setError("");
    }
    _setEmail(text);
  };

  const onJoinWaitlist = () => {
    if (isValidEmail(email)) {
      window.location.href = `${BASE_URL}?email=${email}&code=${code}`;
    } else {
      setError("Invalid email address, try again.");
    }
  };

  // TODO SUT-1370: https://linear.app/sutro/issue/SUT-1370/show-waitlist-on-landing-when-prod-is-down
  useEffect(() => {
    const params = window.location.search;
    if (params.includes("?code=")) {
      const _code = params.split("?code=")[1];
      if (WAITLIST_CODES.includes(_code)) {
        setCode(_code);
      }
    }
    (async () => {
      try {
        await fetch("https://withsutro.com/isAlive");
      } catch (e) {
        setIsProdDown(true);
      }
    })();
  }, []);

  return (
    <section className="w-full flex flex-col relative -z-0">
      {/* <img src={bgCheckerImg} className="absolute" /> */}
      <div className="w-full flex flex-col xl:flex-row xl:justify-between items-center lg:pt-30 pt-28">
        {/* Left side */}
        <div className="flex flex-col xl:items-start items-center">
          <h1 className="xs:text-xl text-lg font-bold">
            Build and ship your own
          </h1>
          <LoopedTypist>
            {HERO_HEADING_TEXTS.map((item, index) => {
              const isLastItem = index === HERO_HEADING_TEXTS.length - 1;
              return (
                <h1
                  key={item}
                  className="xs:text-xl text-lg font-bold text-purple-primary xl:text-left text-center"
                >
                  {item}
                  <Typist.Delay ms={TYPING_DELAY} />
                  <Typist.Backspace
                    count={isLastItem ? 0 : item.length}
                    delay={TYPING_BACKSPACE_DELAY}
                  />
                </h1>
              );
            })}
          </LoopedTypist>
          <p className="text-base max-w-xl pt-8 text-gray-primary xl:text-left text-center">
            Sutro enables entrepreneurs to focus on product ideas, and not on
            the tech
          </p>
          {SHOW_WAITLIST && isProdDown !== true ? (
            <div className="flex flex-col items-center pt-20">
              <div
                className="p-4 rounded-full flex flex-row"
                style={{ backgroundColor: "#191320" }}
              >
                <input
                  placeholder="Enter your email"
                  style={{
                    background: "transparent",
                    // we can apply a min-width to force it to be longer if desired
                    minWidth: 220,
                    outline: "none",
                  }}
                  type="email"
                  className="mx-4 text-sm flex justify-self-stretch"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <button
                  className="bg-purple-primary transition px-8 mx-2 py-2 hover:bg-purple-700 rounded-full text-sm hover:shadow-none shadow-[0_0_4px_2px_rgba(97,8,194,0.57);]"
                  onClick={onJoinWaitlist}
                >
                  {code.length !== 0 ? `Get Early Access` : `Join Waitlist`}
                </button>
              </div>
              {code !== "" && (
                <p className="text-xs mt-2">
                  Access code <b>{code.toUpperCase()}</b> valid
                </p>
              )}
              {error !== "" && (
                <p className="text-xs mt-2" style={{ color: "red" }}>
                  {error}
                </p>
              )}
            </div>
          ) : (
            isProdDown === false && (
              <div className="flex items-center pt-10">
                <button
                  className="bg-purple-primary transition px-12 py-3 hover:bg-purple-700 rounded-md hover:shadow-none shadow-[0_0_4px_2px_rgba(97,8,194,0.57);] font-medium"
                  onClick={() => window.open(BASE_URL)}
                >
                  Get Started
                </button>
              </div>
            )
          )}
        </div>

        {/* Right side */}
        <div className="md:w-[30rem] xs:w-96 w-80 relative xl:self-start justify-center py-14 xl:py-0">
          <img
            src={heroIllustrationImg}
            alt="Sutro Studio"
            // visual hack to align the image in center, the image has some layout issues / extra whitespace
            // in Figma, and I couldn't fix that.
            className="w-auto relative -right-8"
            loading="eager"
          />
        </div>
      </div>

      {/* Built by engineers section */}
      <div className="w-full flex flex-col items-center pt-28">
        <p className="text-gray-primary text-sm text-center">
          Built by engineers from
        </p>

        <div className="w-full flex items-center justify-center pt-7 gap-5 flex-wrap">
          <CompanyLogo name="all" />
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
