import React from "react";

import logoImg from "../../assets/images/logo.svg";

const NavBar = () => {
  const handleLinkClick = (event: Event) => {
    event.preventDefault();

    try {
      const target = event.target as HTMLAnchorElement;
      const sectionTargetEl = document.querySelector(target.hash);

      window.scrollTo({
        top: sectionTargetEl?.getBoundingClientRect().y,
        behavior: "smooth",
      });
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <nav className="w-full flex 2xs:flex-row flex-col items-center justify-between">
      <img src={logoImg} alt="Sutro" className="pb-4 2xs:pb-0 sm:w-auto w-36" />
      <ul className="flex items-center">
        <li>
          <a
            className="px-6 text-sm transition hover:opacity-60"
            href="#about-section"
            onClick={handleLinkClick as any}
          >
            About
          </a>
        </li>
        <li>
          <a
            className="px-6 text-sm transition hover:opacity-60"
            href="#team-section"
            onClick={handleLinkClick as any}
          >
            Team
          </a>
        </li>
        <li>
          <a
            className="pl-6 text-sm transition hover:opacity-60"
            href="https://lime-porcupine-b86.notion.site/Sutro-Engineering-Roles-7034dd7cb43a4a52a0dc27b8bbe2c81e"
            target="_blank"
            rel="noreferrer"
          >
            Careers
          </a>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
